import React, { useState } from 'react';
import Axios from 'axios';
import { Button, List, Modal } from 'antd';
import groundJpg from './ground.jpg';
import list from './playerlist.jpg';
import profile1 from './profile/1.jpg'
import profile2 from './profile/2.jpg'
import profile3 from './profile/3.jpg'

function AttendancePage() {
    const all = [
        { id: 0, name: "", image: profile1, num: 3 }, { id: 1, name: "", image: profile1, num: 18 }, { id: 2, name: "", image: profile1, num: 0 }, { id: 3, name: "", image: profile1, num: 14 }, { id: 4, name: "", image: profile1, num: 17 },
        { id: 5, name: "", image: profile1, num: 77 }, { id: 6, name: "", image: profile1, num: 77 }, { id: 7, name: "", image: profile1, num: 8 }, { id: 8, name: "", image: profile1, num: 32 }, { id: 9, name: "", image: profile1, num: 7 },
        { id: 10, name: "", image: profile1, num: 52 }, { id: 11, name: "", image: profile1, num: 13 }, { id: 12, name: "", image: profile1, num: 19 }, { id: 13, name: "", image: profile1, num: 11 }, { id: 14, name: "", image: profile1, num: 10 },
        { id: 15, name: "", image: profile2, num: 6 }, { id: 16, name: "", image: profile2, num: 16 }, { id: 17, name: "", image: profile2, num: 99 }, { id: 18, name: "", image: profile2, num: 1 }, { id: 19, name: "", image: profile2, num: 6 },
        { id: 20, name: "", image: profile2, num: 2 }, { id: 21, name: "", image: profile2, num: 7 }, { id: 22, name: "", image: profile3, num: 15 }, { id: 23, name: "", image: profile2, num: 5 }, { id: 24, name: "", image: profile2, num: 9 }, 
        { id: 25, name: "", image: profile2, num: 7 }, { id: 26, name: "", image: profile2, num: 17 }, { id: 27, name: "", image: profile2, num: 22 }, { id: 28, name: "", image: profile3, num: 11 },{ id: 29, name: "", image: profile3, num: 30 },
        { id: 30, name: "", image: profile3, num: 4 }, { id: 31, name: "", image: profile3, num: 6 }, { id: 32, name: "", image: profile3, num: 10 }, { id: 33, name: "", image: profile3, num: 20 }, { id: 34, name: "", image: profile3, num: 13 },
        { id: 35, name: "", image: profile3, num: 35 }, { id: 36, name: "", image: profile3, num: 10 }, { id: 37, name: "   ", image: profile3, num: 11 }, { id: 38, name: "", image: profile3, num: '00' }, { id: 39, name: "", image: profile3, num: 27 },
        { id: 40, name: "", image: profile3, num: 12 }
    ];

    // 
    const [members, setMembers] = useState(all);

    //   
    const [attendanceList, setAttendanceList] = useState([]);

    //   
    const handleAttendance = (member) => {
        setAttendanceList([...attendanceList, member]);
        setMembers(members.filter((m) => m !== member));
    };

    //     
    const removeAttendance = (member) => {
        setAttendanceList(attendanceList.filter((m) => m !== member));
        setMembers([...members, member]);
    };

    //     
    const submitAttendanceList = () => {
        Modal.confirm({
            title: '  ',
            content: (
                <div>
                    <p>  : {attendanceList.length}</p>
                    <p> ?</p>
                    <p>    .</p>
                    <p>      .</p>
                </div>
            ),
            okText: '',
            cancelText: '',
            onOk() {
                console.log('  :', attendanceList);
                Axios.post('/api/chart/plusPlays', { attendanceList })
                .then(response => {
                    console.log(response.data)
                    if(response.data.success) {
                        console.log('  .')
                        setAttendanceList([]); //   
                        setMembers(all); //   
                        window.location.reload();
                    } else {
                        alert('  .')
                    }
                })
            },
            onCancel() {
                console.log('');
            },
        });
    };

    let date = new Date();

    return (
        <div style={{ textAlign: 'center', minHeight: "100vh" }}>
            <div style={{ padding: "10px", backgroundImage: `url(${list})`, backgroundSize: 'cover' }}>
                <div style={{ textAlign: "center" }}>
                    <h1 style={{ color: "#fff", fontSize: "25px" }}>{date.toLocaleDateString()} </h1>
                    <p style={{ fontSize: '11px', color: "#fff" }}>        .</p>
                </div>
                <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', minHeight: "50vh" }}>
                    <h2 style={{ color: 'white', fontWeight: 'bold' }}>HABIBI FC PLAYER LIST</h2>
                    <List
                        grid={{ gutter: 10, column: 5 }}
                        dataSource={members}
                        renderItem={member => (
                            <List.Item>
                                <Button onClick={() => handleAttendance(member)} style={{ width: '70px', height: '80px', borderRadius: '3px', fontSize: '12px' }}>
                                <div style={{ position: 'relative', width: '100%', height: '100%' }}>
                                    <img src={member.image} alt={member.name} style={{ marginLeft: '-13px', width: '65px', height: '76px', objectFit: 'cover', borderRadius: '3px' }} />
                                    <span style={{ position: 'absolute', bottom: '10px', left: '-15%', transform: 'translateX(-50%)', color: 'black', fontSize: '18px', fontWeight: 'bold' }}>{member.num}</span>
                                    <span style={{ position: 'absolute', bottom: '-9px', left: '50%', transform: 'translateX(-50%)', color: 'black', fontSize: '15px', fontWeight: 'bold' }}>{member.name}</span>
                                </div>
                                </Button>
                            </List.Item>
                        )}
                    />
                </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', minHeight: "50vh", background: `url(${groundJpg})`, backgroundSize: 'cover', padding: '20px' }}>
                <h2 style={{ color: 'white' }}>TODAY'S PLAYER</h2>
                <List
                    grid={{ gutter: 10, column: 5 }}
                    dataSource={attendanceList}
                    renderItem={member => (
                        <List.Item>
                            <Button onClick={() => removeAttendance(member)} style={{ width: '70px', height: '80px', borderRadius: '3px', fontSize: '12px' }}>
                                <div style={{ position: 'relative', width: '100%', height: '100%' }}>
                                    <img src={member.image} alt={member.name} style={{ marginLeft: '-13px', width: '65px', height: '76px', objectFit: 'cover', borderRadius: '3px' }} />
                                    <span style={{ position: 'absolute', bottom: '10px', left: '-15%', transform: 'translateX(-50%)', color: 'black', fontSize: '18px', fontWeight: 'bold' }}>{member.num}</span>
                                    <span style={{ position: 'absolute', bottom: '-9px', left: '50%', transform: 'translateX(-50%)', color: 'black', fontSize: '15px', fontWeight: 'bold' }}>{member.name}</span>
                                </div>
                            </Button>
                        </List.Item>
                    )}
                />
                <Button type="primary" onClick={submitAttendanceList} style={{ background: '#2a85fb', width: '105px', height: '45px', borderRadius: '6px', fontSize: '15px' }}> </Button>
            </div>
        </div>
    );
};

export default AttendancePage;